<template>
    <span>
        <v-card-title  primary class="title" style="height:25%;width:100%;">{{title}}</v-card-title>
        <v-card-actions  ref="chartContainer"  style="height:120px;width:100%;padding-top:0px;">
            <highcharts v-if="load" style="height:100px; width:100%;" :options="chartOptions"></highcharts>
        </v-card-actions>
    </span>
</template>

<script>

    import {Chart} from 'highcharts-vue'
    import More from 'highcharts/highcharts-more'
    import Highcharts from 'highcharts'
    import Exporting from 'highcharts/modules/exporting'

    Exporting(Highcharts)

    More(Highcharts)

    export default {
        name: "ChartFrequentIssues",
        components: {
             highcharts: Chart
        },
        props: {
            active:Boolean,
            title:String,
            flat:Boolean,
            chartOptions:Object
        },
        data: function () {
            return {
               load:false
            }
        },
        methods: {
            onResize(event)
            {
                this.load = false;

                setTimeout(() => {
                    this.chartOptions.chart.height =this.$refs['chartContainer'].clientHeight;
                    this.load=true;});
            }
        },
        created() {
            window.addEventListener('resize', this.onResize);

        },
        mounted() {
            setTimeout(() => {
                    this.chartOptions.chart.height =this.$refs['chartContainer'].clientHeight;
                    this.load=true;});
        },
        watch:
        {
            active : function()
            {
                this.load = false;

                setTimeout(() => {
                    this.chartOptions.chart.height =this.$refs['chartContainer'].clientHeight;
                    this.load=true;});

            },
            chartOptions:function()
            {
                this.load = false;

                setTimeout(() => {
                    this.chartOptions.chart.height =this.$refs['chartContainer'].clientHeight;
                    this.load=true;});

            }
        }
    }
</script>

